
/* IMPORT FONT ASSETS */
@font-face {
    font-family: 'Cursed Timer';
    src: url(../fonts/cursed-timer.ttf);
}

/* VERTICALLY ALIGN CONTENT TO CENTER */
#timer-outer-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

/* DEFINE DEFAULT TIMER SIZE */
#timer-display {
    font-size: 75px;
    min-height: 114px;
    border: 2px solid gray;
    border-radius: 5px;
}
#timer-display-ms {
    font-size: 25px;
}

/* MODIFY TIMER SIZE FOR SCREEN */
@media only screen and (min-width: 450px) {
    #timer-display {
        font-size: 100px;
        min-height: 152px;
    }
    #timer-display-ms {
        font-size: 33px;
    }
}
@media only screen and (min-width: 700px) {
    #timer-display {
        font-size: 150px;
        min-height: 228px;
    }
    #timer-display-ms {
        font-size: 50px;
    }
}
@media only screen and (min-width: 900px) {
    #timer-display {
        font-size: 200px;
        min-height: 304px;
        border: 2px solid gray;
        border-radius: 5px;
    }
    #timer-display-ms {
        font-size: 67px;
    }
}

/* ARRANGE DISPLAY SIDE-BY-SIDE FOR HORIZONTAL SCREENS */
@media only screen and (min-aspect-ratio: 2/1) and (orientation: landscape) {
    #timer-inner-container {
        display: flex;
    }
    #timer-display {
        flex-grow: 1;
    }
    #timer-control-panel {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}

/* DEFINE FONT FACES */
#timer-display-min, #timer-display-sec, #timer-display-ms {
    font-family: 'Cursed Timer', 'Titillium Web', Arial, sans-serif;
}
#timer-display-signal {
    font-family: 'Titillium Web', Arial, sans-serif;
}

/* ADD COLONS */
#timer-display-min::after {
    content: ':';
}
#timer-display-ms::before {
    content: ':';
}

/* CONTROL PANEL ALIGNMENT AND SIZING */
#timer-control-panel {
    text-align: left;
    min-width: 210px;
}
#timer-status-container {
    font-size: 30px;
}
p {
    margin: 0;
}
.timer-button {
    margin: 5px;
    padding: 15px;
    border: 2px solid black;
    border-radius: 10%;
}

/* BUTTON COLORS */
#play-button, #pause-button {
    background: #BFB;
}
#stop-button {
    background: #FBB;
}
#play-button:hover, #pause-button:hover {
    background: #6D6;
}
#stop-button:hover {
    background: #D66;
}
